.booklist {
  display: grid;
  flex-direction: column;
  flex-wrap: wrap;
}

.booklist__title {
  align-self: center;
  font-size: 2.5em;
}

.booklist__section {
  display: grid;
  gap: 20px 20px;
  grid-template-rows: repeat(auto-fill, 255px);
  grid-template-columns: repeat(auto-fill, 185px);
}
